import { Card, CardMedia, Dialog } from "@material-ui/core";
import React from "react";

const ImageModalComponent = ({ source, alt, toggle, handleToggle }) => {
    return (

        <Dialog
            className="img-big"
            open={toggle}
            onClick={() => handleToggle()}
        >
            <Card>
                <CardMedia
                    component="img"
                    className="modal-image"
                    image={source}
                    alt={alt}
                />
            </Card>
        </Dialog >
    );
}
export default ImageModalComponent;