import { Avatar, Container, Grid, List, ListItem, ListItemAvatar, ListItemText, Typography } from "@material-ui/core";
import React from "react";
import "../style/_Main.scss";
import { Icon } from '@iconify/react';
import CardItem from "../components/CardItem";
import _ from "lodash";

import { skills, takenPhotoLength, personalPhotoLength, basePath, extension } from "../Constants";
const Copyright = () => {
    return <div className="copyright typos">
        Copyright  {new Date().getFullYear} by {<a href="https://www.github.com/sofisticat">sofisticat</a>}.
    </div>
}

const Contact = () => {
    return <Container className="contact">
        Contact Me
        <p>Mail Address: <a href="mailto:serhatuyanmis@gmail.com">serhatuyanmis@gmail.com</a></p>
        <p>Phone: <a href="tel:+905415526978">(+90)5415526978</a></p>
        <a href="https://t.me/sofisticat">Telegram</a>
    </Container>
}
const MainPage = () => {
    const renderSkills = () => {
        return (
            <List>
                {_.map(skills, (skill) => {
                    return (<ListItem className="skills-item">
                        <ListItemAvatar>
                            <Avatar>
                                <Icon width="26" height="26" icon={skill.icon} style={{ color: skill.color }} />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText
                            className=""
                            primary={<Typography className="skills-item__title" type="div">{skill.primary}</Typography>}
                            secondary={<Typography className="skills-item__text" type="p">{skill.secondary}</Typography>} />
                    </ListItem>)
                })}
            </List>
        );
    }
    const renderCards = () => {
        const items = [];
        for (let i = 1; i <= takenPhotoLength; i++) {
            const imageName = `taken-photo-${i}`;
            const imagePath = `${basePath}/${imageName}${extension}`;
            items.push({ name: imageName, path: imagePath, explanation: "My Favorite Shots" });
        }
        for (let i = 1; i <= personalPhotoLength; i++) {
            const imageName = `personal-photo-${i}`;
            const imagePath = `${basePath}/${imageName}${extension}`;
            items.push({ name: imageName, path: imagePath, explanation: "YOLO" })
        }
        return _.map(items, (item) => {
            return <Grid item xs={12} md={6} lg={4}>
                <CardItem
                    alt={item.name}
                    key={item}
                    image={item.path}
                    content={item.explanation}
                />
            </Grid>

        });
    }

    return <div className="container">
        <p className="typos-description">
            Hey, I am <a className="name" href="https://www.github.com/filiumbelli">Serhat</a>. I would love to call myself as sofisticat (my secondary account) in the Software Universe.
            Here I include my professional skills and all that kind of stuff that makes me who really I am.
            <p>If you can not find me in front of my computer, I am probably singing and playing instruments,hanging out with my friends or just sleeping somewhere warm and uncomfortable <span role="img" aria-label="sheep"></span></p>
        </p>
        <div className="container-section">
            <h4 className="header-typos skills">My Professional Skills</h4>
            <div className={"container-skills"}>
                {renderSkills()}
            </div>

        </div>
        <div className="container-section">
            <h4 className="header-typos life">My Personal Life</h4>
            <Grid container spacing={2}>
                {renderCards()}
            </Grid>
        </div>
        <Contact />
        <Copyright />

    </div>
}

export default MainPage;